<script>
const trekkingURL = 'https://delivery-spring.herokuapp.com/rest/package/getMyRequests'
import MenuBar from '@/components/MenuBar'
import NavBar from '@/components/NavBar'
import InfoCard from '@/components/InfoCard'
import PackageList from '@/components/PackageList'
import axios from "axios";

export default {
  name: "Trekking",
  data(){
    return{
      language: '',
      message: '',
      tracks: {},

      itemTypeSort: 'undefined',
      status: 'undefined',
      sortTime: 'new',
    }
  },
  components: {
    MenuBar, NavBar, InfoCard, PackageList
  },
  created() {
    this.language = localStorage.language
    this.getTrekk()
  },
  methods:{
    getTrekk(){
      axios
          .get( trekkingURL, {
            headers: {
              'Authorization': localStorage.token
            }})
          .then(response => {
            this.tracks = response.data
          })
    }
  },
  watch: {
    language() {
      switch (this.language) {
        case '0':
          this.message = 'This is trekking page'
          break
        case '1':
          this.message = 'Aceasta este pagina de drumeii'
          break
        case '2':
          this.message = '  '
          break
        case '3':
          this.message = ''
          break
      }
    },
    sortTime(){
      this.tracks.reverse()
    }
  },
}
</script>

